<template>
    <div class="order">

        <div id="overlay" v-if="showInst" style="position: absolute; width: 100%; height: calc(100% - 106px); background: rgba(0,0,0,.5); z-index: 999; backdrop-filter: blur(5px);">
            <div id="instr">
                <div style="margin-left: auto; margin-right: 0px; width: 50px">
                    <svg @click="closeInst()" xmlns='http://www.w3.org/2000/svg' class='ionicon' viewBox='0 0 512 512'><title>Close</title><path fill='none' stroke='currentColor' stroke-linecap='round' stroke-linejoin='round' stroke-width='32' d='M368 368L144 144M368 144L144 368'/></svg>
                </div>

                <h3 style="text-align: left">Instructions:</h3>
                <ol style="text-align: left">
                    <li>Create a design here. Ensure all options are to your liking</li>
                    <li>Add as many designs to cart as wanted</li>
                    <li>Go to cart. Add email and shipping information</li>
                    <li>Ensure all information is correct and submit your order!</li>
                    <li>After submitting a Paypal invoice will be emailed to you in order for you to pay</li>
                </ol>

                <p>Please be aware that not all options show in design editor. For instance, color and exhaust do not show.</p>
            </div>
        </div>

        <h2>Create a New Order</h2>

        <div id="design">
            <img :src="require(`../assets/images/product/blank_${shape}.png`)" style="width :  90%; max-width: 1000px"/>
            <h3 id="big-text" :style="`font-family : ${this.font}`">{{text}}</h3>

        </div>

        <h4>Text</h4>
        <input type="text" v-model="text"><br>

        <h4>Shape</h4>
        <select id="shape" v-model="shape">
            <option value="round">Round</option>
            <option value="norm">Normal</option>
        </select><br>

        <h4>Font</h4>
        <select id="font" v-model="font">
            <option v-for="f in fonts" :key="f" :value="f">{{f}}</option>
        </select><br>

        <button :disabled="text == ''" v-on:click="addToOrder">Add to Cart</button>

    </div>
</template>

<script>
import Vue from 'vue';
import Toasted from 'vue-toasted';
Vue.use(Toasted);

export default {
    name : 'Order',
    data() {
        return {
            font : 'Book Antiqua',
            shape : 'norm',
            color : null,
            text : '',
            fonts: ['Book Antiqua' ,'BrushScript', 'Trebuchet'],
            showInst : (localStorage.getItem('inst') == null || localStorage.getItem('inst') == 'true') ? true : false
        }
    },
    methods : {
        async closeInst() {
        
            document.getElementById('overlay').style.opacity = 0;

            await new Promise(resolve => setTimeout(resolve, 440));

            this.showInst = false;

            localStorage.setItem('inst', this.showInst);
        },
        addToOrder() {
            console.log('adding to order!');

            let order = {
                shape : this.shape,
                text : this.text,
                font : this.font,
                price : 139.99
            }

            let cart = JSON.parse(localStorage.getItem('cart'));

            if (cart == null)
                cart = [];

            console.log(cart);

            cart.push(order);

            localStorage.setItem('cart', JSON.stringify(cart));

            console.log(order);

            this.$emit('cartInc');

            this.text = '';

            let toast = this.$toasted.show("<p>Added to Cart</p>", { 
                theme: "outline", 
                position: "bottom-center", 
                duration : 2000,
                className: "custom-toast"
            });

            console.log(toast.el);
        }
    },
    created() {

    }
}
</script>

<style scoped>

h2 {
    color : black;
    padding-top: 20px;
}

#instr {
    width: 80%; 
    background: rgb(112, 112, 112); 
    margin: auto;
    margin-top: 10%;
    color: white;
    padding: 20px;
    border-radius: 15px;
    
}

#overlay {
    transition: all .44s;
}

svg {
    width: 50px;
}

svg:hover {
    cursor: pointer;
}

h4 {
    margin-bottom: 0px;
    margin-top: 10px;
}

#design {
    position: relative;
    text-align: center;
}

#big-text {
    position:absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999; 
    color: rgb(199, 206, 137);
    font-size: min(9vw, 110px);
    font-weight: normal;
    -webkit-text-stroke: 2px black;
}

button {

    margin-top: 15px;
    background-color: rgba(255,255,255,.05); 
    color: black; 
    border: 1px solid black;
    transition: all .25s;
}

button:disabled {
    cursor: auto;
    opacity: .6;

}

button:hover:enabled {
    cursor: pointer;
    background-color: rgba(0,0,0,.1)
}

</style>