<template>
    <div class="cart">
        <h2>Shopping Cart</h2>

        <h4 v-if="items.length == 0">Cart is Empty!</h4>

        <div class="item" v-for="(i,k) in items" :key="k">
            <div style="margin-left: auto; margin-right: 0px; width: 40px">
                <svg @click="deleteItem(k)" xmlns='http://www.w3.org/2000/svg' class='ionicon' viewBox='0 0 512 512'><title>Close</title><path fill='none' stroke='currentColor' stroke-linecap='round' stroke-linejoin='round' stroke-width='32' d='M368 368L144 144M368 144L144 368'/></svg>
            </div>

            <div style="display : inline-block; text-align: left; width: 35%; margin-top: 0px">
                <h4>Text</h4>
                <h3>{{i.text}}</h3>

                <h4>Shape</h4>
                <h3>{{i.shape}}</h3>

                <h4>Font</h4>
                <h3>{{i.font}}</h3>
            </div>
            
      
            <img :src="require(`../assets/images/product/blank_${i.shape}.png`)" style="width : 65%; display: inline-block;"/>
         
            

            <h3 style="margin-top: 10px; font-size: 24px">${{i.price}}</h3>
        </div>

        <div id="form" v-if="items.length > 0">
            <form> 
                <p v-if="errors.length">
                    <b>Please correct the following error(s):</b>
                    <ul>
                    <li v-for="error in errors" :key="error">{{ error }}</li>
                    </ul>
                </p>

                <label >Full Name</label><br>
                <input type="text" v-model="name"><br>

                <label >Email</label><br>
                <input type="text" v-model="email"><br>

                <label>Address</label><br>
                <input type="text" v-model="address"><br>

                <label >City</label><br>
                <input type="text" v-model="city"><br>
                
                <label>Zip</label><br>
                <input type="text" v-model="zip"><br>
            </form>
        </div>

        

        <div style="position: fixed; bottom: 0; background: rgba(0,0,0,.8); width: 100%; backdrop-filter: blur(5px);" v-if="items.length > 0">
            <h2 style="margin: 10px; text-align: left; color: white; display: inline-block; text-align: left; float: left" v-if="items.length != 0">Total: ${{items.reduce((acc, curr) => acc + curr.price, 0).toFixed(2)}}</h2>

            <button @click="checkForm">Submit Order</button>
        </div>
        
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name : 'Cart',
    data() {
        return {
            items : localStorage.getItem('cart') == null ? [] : JSON.parse(localStorage.getItem('cart')),
            name : null,
            address : null,
            city : null,
            state : null,
            email : null,
            zip : null,
            errors : [],
            customerInfo : null,
            functionUrl : 'https://us-central1-antiqueboatsigns-1cbf4.cloudfunctions.net/createOrder'
        }
    },
    methods : {
        checkForm(e) {
            // regex for patterns
            var zipCodePattern = /^\d{5}$|^\d{5}-\d{4}$/;
            var emailPattern = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

            this.errors = [];
            let allGood = true;

            if (!zipCodePattern.test(this.zip)) {
                this.errors.push("Please review zip code");
                allGood = false;
            }
            if (!emailPattern.test(this.email)) {
                this.errors.push("Please review email");
                allGood = false;
            }
            if (this.name == "" || this.name == null ) {
                this.errors.push("Please enter name");
                allGood = false;
            }
            if (this.address == "" || this.address == null) {
                this.errors.push("Please enter address");
                allGood = false;
            }
            if (this.city == "" || this.city == null) {
                this.errors.push("Please enter city");
                allGood = false;
            }

            e.preventDefault();

            if (allGood) {
                this.submitOrder();
            }

        },
        async deleteItem(k) {
            console.log(k);

            let items = document.getElementsByClassName('item');

            console.log(items[k]);

            items[k].style.opacity = 0;

            await new Promise(resolve => setTimeout(resolve, 250));

            items[k].style.opacity = 1;

            this.items.splice(k,1);
            localStorage.setItem('cart',JSON.stringify(this.items));
            this.$emit('cartInc');
        },
        async submitOrder() {

            let customerInfo = {
                name : this.name,
                address : this.address,
                city : this.city,
                zip : this.zip,
                email : this.email
            }

            await axios.post(this.functionUrl, {order : this.items, customerInfo : customerInfo}).then(r => {
                console.log(r.data);
            }).then(() => {
                this.items = [];
                localStorage.setItem('cart',JSON.stringify(this.items));
            });

            this.$emit('cartInc');
        }
    }
}
</script>

<style scoped>

h2 {
    margin-top: 20px;
    color: black;
}

h3 {
    text-align: left;
    margin: 0px;
    margin-bottom: 10px;
}

h4 {
    margin: 0px;
    color: rgb(102, 102, 102);
}

svg {
    width: 40px;
    left: 0;
    cursor: pointer;
}

.item {
    border: 1px black solid;
    padding: 20px;
    width: 50%;
    margin: auto;
    margin-top: 10px;
    transition: all .25s;
}

#form {
    border: 1px black solid;
    padding: 20px;
    width: 50%;
    margin: auto;
    margin-top: 10px;
}

.cart {
    margin-bottom: 100px;
}

button {
    display: inline-block; 
    float: right; 
    height: 58px; 
    background-color: rgba(255,255,255,.05); 
    color: white; 
    border: 1px solid white;
    transition: all .25s;
}

button:hover {
    cursor: pointer;
    background-color: rgba(255,255,255,.1)
}

</style>